@import url(https://fonts.googleapis.com/css?family=Monoton);
@import url(https://fonts.googleapis.com/css?family=Lekton);
@import url(https://fonts.googleapis.com/css?family=Staatliches);
body {
  margin: 0;
  background: rgb(22, 22, 22) !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.back {
  height: 100vh;
  width: 100vw;
  background: rgb(22, 22, 22) url(/static/media/background.786f5088.jpg) no-repeat right top;
  background-size: 100%;
}

.max {
  max-width: 1400px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.texts {
  width: 520px;
}

.main {
  position: relative;
  top: 80px;
  left: 40px;
  width: 70%;
}

.profile-pic {
  width: 220px;
  border: 2px solid white;
  border-radius: 50%;
  box-shadow: 5px 5px 10px black;
}

.header-asserts {
  padding: 0 !important;
  min-width: 150px;
}

.header-content {
  min-width: 150px;
}

.menu-list {
  margin-top: 30px;
  padding: 0;
}

.menu-list li {
  list-style-type: none;
  padding: 20px 20px;
  width: 130px;
}

.active {
  color: greenyellow;
}

.menu-list li a {
  font-size: 1.3em;
  color: white;
  font-family: 'Staatliches';
  text-decoration: none;
}

.menu-list-icon li {
  list-style-type: none;
  margin: 20px 0;
}

.menu-list-icon li a {
  color: rgb(165, 241, 50);
  font-size: 3em;
  text-decoration: none;
}

.menu-list-icon li a:hover,
.menu-list li a:hover {
  color: rgb(165, 241, 50);
}

.logo-img {
  width: 200px;
  padding: 25px 50px;
}

.card-body {
  padding: 5px !important;
}

.card-title {
  font-size: 16px;
}

.card-text {
  font-size: 14px;
  padding: 2px;
  margin: 0;
}

.project-card {
  box-shadow: 2px 2px 10px black;
  border-radius: 15px;
  border: transparent;
  cursor: pointer;
  height: 340px;
  margin: 0px 5px 20px;
  color: black;
  width: 290px;
  transition: transform 0.4s;
}

.project-card:hover {
  transform: scale(1.05, 1.05);
  border: 2px solid greenyellow;
  color: black;
  text-decoration: none;
}

.project-img {
  height: 45%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.project-title {
  font-weight: 700;
}

.features {
  margin-top: 0px;
}

.under-construction {
  position: relative;
  bottom: 200px;
  left: 50px;
  padding: 0 10px;
  background-color: yellow;
  border: 1px solid black;
  border-radius: 10px;
  font-weight: 900;
}

.titles {
  color: greenyellow;
  margin-left: 30px;
  padding: 20px 10px;
  font-weight: 600;
  text-shadow: 2px 2px 5px black;
}

.lamp {
  width: 50px;
  margin: 5px 20px;
}

.projects {
  margin-left: 25px;
}

.App {
  text-align: center;
  border-radius: 10px;
  padding: 80px;
  padding: 80px;
  width: 700px;
  background-color: white;
}

.button-position,
.twitter-button-position {
  margin: 10px 5px;
}

.wrapper {
  width: 100%;
  height: 100vh;
}

.back {
  margin: 0;
}

.max {
  max-width: none;
}

.timeline {
  padding: 50px;
}

.sizer {
  height: 120px;
}

.column {
  padding: 20px 0 0 50px;
}

.experience-container-title {
  color: greenyellow;
  font-weight: 550;
  padding: 7px;
  text-shadow: 2px 2px 5px black;
}

.row-att {
  color: white;
  margin: 0;
}

.color-advise {
  color: white;
  font-family: 'Lekton';
  height: 5px;
  font-size: 14px;
  font-weight: 700;
  position: relative;
  bottom: 10px;
  text-shadow: 2px 2px 5px black;
  width: 70%;
}

.color-advise {
  color: #adff2f;
  text-align: center;
  -webkit-animation: blink 3s infinite;
          animation: blink 3s infinite;
}

.column-competencies {
  padding-left: 50px;
  width: 75%;
}

.comp-badges {
  padding: 5px 20px;
}

.badge-container {
  background-color: rgba(255, 255, 255, 0.3);
  border: 2px solid white;
  border-radius: 20px;
  height: 230px;
  padding: 5px;
  margin: 5px;
}

.languages {
  width: 330px;
}

.libraries {
  width: 550px;
}

.frameworks {
  width: 300px;
}

.combined {
  width: 580px;
}

body {
  background-color: rgb(221, 221, 223);
  background-size: cover;
  min-width: 600px;
}

.title {
  color: white;
  font-family: 'Monoton';
  font-size: 50px;
  font-weight: 550;
  text-shadow: 2px 2px 5px black;
}

.subtitle {
  color: rgb(165, 241, 50);
  font-family: 'Lekton';
  font-size: 30px;
  font-weight: 600;
  text-shadow: 2px 2px 5px black;
}

.phrase0 {
  color: white;
  font-family: 'Lekton';
  font-size: 25px;
  font-weight: 700;
  text-shadow: 2px 2px 5px black;
}

.menu-list-icon-contact {
  height: 60px;
  padding: 0;
}

.menu-list-icon-contact li {
  list-style-type: none;
}

.menu-list-icon-contact li a {
  color: white;
  font-size: 50px;
  padding: 0 5px;
}

.menu-list-icon-contact li a:hover {
  color: #adff2f;
}

.contact-badges {
  width: 200px;
}

